.main_portion{
  background-color: rgb(238, 238, 236);
  border-radius: 10px;
  padding:25px 15px;
  box-shadow: 0 0 10px grey ;
}
a{
    text-decoration: none;
    color: lightslategray;
}

a:hover{
    text-decoration: none;
    color: black;
}


.sidebar_avatar{
    height: 250px;
    width: 250px;
    object-fit: contain;
    border-radius: 20px;
}

.sidebar{
  background-color: rgb(212, 217, 221);
  height: 100%;
  border-radius: 10px;
  padding:25px 15px;
  box-shadow: 0 0 10px grey ;
  text-align: center;
}

.sidebar_icon{
    width: 30px;
    height: 30px;
    margin-left: 18px;
    margin-right: 10px;
}

.sidebar_name{
    font-family:'Times New Roman', Times, serif;
    margin: 20px 0;
    font-size: 1.8rem;
    

}

.sidebar_name{
    color: rgb(14, 13, 13);
   
}
.sidebar_item{
    margin-bottom: 6px;
    margin-top:13px;
    height: 40px;
    font-size: 1.3rem;
    border-radius: 30px;
    

}




.sidebar_title{
    background-color: #bdc3c7;
    border-radius: 10px;
}

.contact{
    background-color:#bdc3c7;
    padding: 60px 2px;
    margin-top: 20px 0;
    margin: 20px -15px;
    border-radius: 10px;
   
   
}

.sidebar_email{
    background-color: cadetblue;
    border-radius: 50px;
    margin-top: 80px;
    color: #fff;
    margin: 30px 25px 0px;
    cursor: pointer;
    transition: all 3s linear;
    

}

.sidebar_email:hover{
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
}

.sidebar_github:hover,
.sidebar_resume:hover{
    background-color: #bdc3c7;
    color: black;
    border-radius: 10px;

}

@media(max-width:991px){
    .main_portion{
        margin: 30px 0;

    }
}
.resume {
    padding-top: 30px;
}

.resume-card__heading,
.resume-language__heading {
    margin-left: 0px;
    font-size: 1.8rem;
    font-weight: 700;
    color: #354538;
   
}

.resume-card__body {
    margin-top: 30px;
    
}

.resume-languages{
    margin-top: 50px;
}

.resume-card__title {
    font-weight: 700;
}

.resume-card__name {
    font-size: 16px;
    
}

.resume-card__details {
    font-size: 2rem;
    padding-top: 5px;
}

/* bar */
.bar {
    width: 90%;
   
    border-radius: 20px;
    margin-bottom: 12px;

}

.bar__icon {
    width: 20px;
    height: 20px;
    margin-left: 1px;
    color: white;
    
}

.bar__wrapper {
    background-image: linear-gradient(90deg, #0F2027, #203A43,#2C5364);
    border-radius: 5px;
    padding: 4px 14px;
    color: #fff;
    font-size: 1rem;
    
    
}
body{
    background-color: #FA8BFF;
    background-image: linear-gradient(45deg, #FA8BFF 0%, #2BD2FF 52%, #2BFF88 90%);
    
 } 



.header{
    font-family:'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    padding: 30px;
    font-weight: 400;
    margin-top: -5%;
    text-align: center;
    
    background-image: linear-gradient(15deg, #13547a 0%, #80d0c7 100%);
    color: white;
    font-size: 40px;
    text-transform: uppercase;

    
    
    
    
    
    border:4px solid #2c2c31;
    
    
    outline: none;
    
    border-radius: 6px;
    transition: 0.25s;
  }
  


     

.info{
    width:400px;
    padding: 10px;
    position:absolute;
    top:70%;
    left: 20%;
    -webkit-transform: translate(-50%,-50%);
            transform: translate(-50%,-50%);
    background: #191919;
    text-align: center;
    border:4px solid #0b9de6;
    border-radius: 15px;
}
.info h2{
    color: white;
    text-transform:uppercase;
    font-weight: 400;
}
.info input{
    border:0;
    background: none;
    display: block;
    margin: 15px auto;
    text-align: left;
    border:2px solid #3498db;
    padding: 3px 2px;
    width: 200px;
    outline: none;
    color:white;
    border-radius: 6px;
    transition: 0.25s;
}

.info input {
    width: 85%;
}

.info input:focus{
    width: 330px;
    border-color: #2ecc71;
}







