.btn {background-image: linear-gradient(to right, #314755 0%, #237aa2  51%, #314755  100%)}



.btn {
   margin: 50px;
   padding: 20px 163px;
   align-items: center;
   text-align: center;
   text-transform: uppercase;
   transition: 0.5s;
   background-size: 200% auto;
   color: rgb(245, 245, 245);            
   box-shadow: 0 0 20px rgb(18, 18, 18);
   border-radius: 10px;
   display: block;
   margin-top: 83rem;
   margin-left: 27.8rem;
   border:2px solid #3498db;
   
 
 }


 .btn:hover {
   background-position: right center; /* change the direction of the change here */
   color: #fff;
   text-decoration: none;
 }

